<template>
  <div class="register">
    <van-nav-bar
      left-arrow
      title="手机注册"
      @click-left="goPage('Login')"
    />

    <van-form>
      <van-field
        type="text"
        placeholder="请输入手机号"
        v-model="userInfo.phone"
        maxlength="11"
      >
        <!-- 自定义右边图标 -->
        <template v-slot:left-icon>
          <svg t="1593845914164" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3887" width="32" height="32"><path d="M512 821.248c-28.672 0-52.224 23.552-52.224 52.224s23.552 52.224 52.224 52.224 52.224-23.552 52.224-52.224-23.552-52.224-52.224-52.224z" fill="#707070" p-id="3888"></path><path d="M770.048 0H253.952c-43.52 0-78.848 35.328-78.848 78.848v866.304c0 43.52 35.328 78.848 78.848 78.848h516.096c43.52 0 78.848-35.328 78.848-78.848V78.848c0-43.52-35.328-78.848-78.848-78.848zM253.952 52.224h516.096c14.336 0 26.112 11.776 26.112 26.112v649.216H227.328V78.848c0.512-14.848 12.288-26.624 26.624-26.624z m516.096 919.04H253.952c-14.336 0-26.112-11.776-26.112-26.112v-160.256h568.832v160.256c-0.512 14.336-12.288 26.112-26.624 26.112z" fill="#707070" p-id="3889"></path></svg>
        </template>
        
      </van-field>

      <van-field
        type="text"
        placeholder="请输入用户名"
        v-model="userInfo.nickName"
        maxlength="6"
      >
        <!-- 自定义右边图标 -->
        <template v-slot:left-icon>
          <svg t="1594434382762" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2086" width="32" height="32"><path d="M652.3 562.7C741.6 513.3 802 418.2 802 309c0-160.2-129.8-290-290-290S222 148.8 222 309c0 109.2 60.4 204.3 149.7 253.7C184 622.2 48 797.7 48 1005h58c0-224.2 181.8-406 406-406s406 181.8 406 406h58c0-207.3-136-382.8-323.7-442.3zM280 309c0-128.1 103.9-232 232-232s232 103.9 232 232-103.9 232-232 232-232-103.9-232-232z" p-id="2087"></path></svg>
        </template>

        <!-- <template v-slot:right-icon>
          <van-button type="primary" size="normal">获取验证码</van-button>
        </template> -->
        
      </van-field>

      <van-field
        class="sock"
        v-model="userInfo.password"
        :type="isEye ? 'text' : 'password'"
        placeholder="请输入密码"
        :right-icon="isEye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="viewPassword"
        maxlength="16"
      >
        <!-- 自定义右边图标 -->
        <template v-slot:left-icon>
          <svg t="1593847235855" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5797" width="32" height="32"><path d="M793.81801 384.050662H762.531772v-122.190212C762.531772 115.051773 658.527584 0 510.816797 0 362.530751 0 261.468228 117.470475 261.468228 261.86045v122.190212H230.155842C161.124778 384.050662 104.906298 441.41966 104.906298 512.045759v383.94607c0 70.599951 56.21848 128.008171 125.249544 128.008171h563.662168c69.096434 0 125.275692-57.40822 125.275692-128.008171V512.045759c0-70.599951-56.21848-127.995097-125.275692-127.995097z m-469.738085-122.190212c0-109.037703 72.979431-197.797531 186.736872-197.797531 112.580774 0 189.064055 86.916385 189.064055 197.849827v122.190212H324.079925z m532.349783 634.092157c0 35.299976-28.083092 64.062919-62.611698 64.062918H230.155842c-34.54168 0-62.637846-28.762943-62.637846-64.062918V512.045759c0-35.299976 28.083092-63.984475 62.637846-63.984474h563.662168c34.528606 0 62.611698 28.762943 62.611698 63.984474zM511.980389 575.977938c-34.567828 0-62.611698 28.762943-62.611698 64.062918a64.167511 64.167511 0 0 0 31.312386 55.420962v104.592521a31.377756 31.377756 0 1 0 62.611698 0v-104.592521a64.219808 64.219808 0 0 0 31.377756-55.420962c0-35.299976-28.056944-64.062919-62.637846-64.062918z m0 0" p-id="5798"></path></svg>
        </template>
        
      </van-field>
      <div class="register-btn">
        <van-button block type="info" native-type="submit" @click="register">立即注册</van-button>
      </div>
      <van-checkbox v-model="checked" shape="square">我已认真阅读并同意<span>该用户协议</span></van-checkbox>
    </van-form>
  </div>
</template>

<script>

  //导入验证表单模块
  import validForm from '../assets/js/validForm'

  export default {
    data() {
      return {
        checked: false,
        userInfo: {
          phone: '',
          nickName: '',
          password: ''
        },
        //false: 闭合，true：睁开
        isEye: false
      };
    },
    methods:{

      //跳转页面
      goPage(name) {
        this.$router.push({name});
      },

      //切换密码的格式
      viewPassword() {
        this.isEye = !this.isEye;
      },

      //注册
      register() {

        if(this.checked = this.checked){
          //进行验证表单
          if (validForm.validUserForm(this.userInfo)) {

            //复制对象
            let data = Object.assign({}, this.userInfo);

            //请求密钥
            data.appkey = this.appkey;

            //开启加载提示
            this.$toast.loading({
              message: '加载中...',
              forbidClick: true,

              //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
              duration:0
            });

            //发起注册请求
            this.axios({
              method: 'POST',
              url: '/register',

              //post传递参数需要写在data
              data
            }).then(result => {
              //关闭加载提示
              this.$toast.clear();
              
              if (result.data.code == 100) {
                //跳转到登录页面
                this.$router.push({name: 'Login'});
              } else {
                this.$toast(result.data.msg);
              }
            }).catch(err => {
              //关闭加载提示
              this.$toast.clear();
              
            })
          }
        }else{this.$toast('请勾选用户协议')}
      }
    }
  }
</script>

<style lang="less" scoped>
  .van-nav-bar{
    background-color: rgb(0,136,214);
  }
  /deep/.van-nav-bar__title{
    color: white;
    font-weight: bold;
  }
  /deep/.van-nav-bar__left .van-icon{
    color: white;
  }
  /deep/.van-field__control{
    height: 40px;
  }
  svg{
    margin-top: 4px;
  }
  /deep/.van-field__left-icon{
    height: 40px;
  }
  .van-field{
    margin-top: 20px;
    padding-right: 0;
  }
  .register-btn{
    margin: 40px auto;
    margin-bottom: 20px;
    
    width: 85%;
  }
  /deep/.van-button{
    border-radius: 7px;
    background-color: rgb(0,136,214);
  }
  .van-button__text{
    font-size: 20px;
  }
  .van-field__right-icon{
    height: 40px;
  }
  .van-cell .van-button{
    height: 40px;
    background-color: rgb(229,244,251);
    border:0.02667rem solid rgb(229,244,251);
    color: rgb(0,136,214);
  }
  .van-cell .van-button .van-button__text{
    font-size: 16px;
  }
  /deep/.van-checkbox__label{
    margin-left: 0;
  }
  .van-checkbox{
    width: 320px;
    margin: 20px auto;
  }
  /deep/.sock .van-field__right-icon{
    margin-right: 20px;
  }
</style>